// React/club-manager/src/api/services/socioService.js
import api from '../axios';
import endpoints from '../endpoints';

/**
 * Servizio per la gestione dei soci
 */
const socioService = {
  /**
   * Recupera l'elenco dei soci in base ai filtri
   * @param {string|null} cognome - Cognome del socio 
   * @param {boolean} scadenza - Flag per mostrare solo soci con certificato scaduto
   * @param {number} attivita - ID attivit (0 per tutte, specifico ID per attivit singola)
   * @param {boolean} scadute - Flag per mostrare solo soci con abbonamento scaduto (solo se attivita  specificata)
   * @param {number} anno - Anno di riferimento
   * @param {number} sezione - ID sezione (utilizzato quando attivita = 0)
   * @returns {Promise} Promise con i dati dei soci
   */
  retrieveSocio: (cognome = null, scadenza = 0, attivita = 0, scadute = 0, anno = 0, sezione = 0) => {
    const url = `${endpoints.SOCIO.RETRIEVE}/${cognome || 'null'}/${scadenza}/${attivita}/${scadute}/${anno}/${sezione || 0}`;
    return api.get(url);
  },
  
  /**
   * Recupera un socio in base al suo ID
   * @param {number} id - ID del socio
   * @returns {Promise} Promise con i dati del socio
   */
  retrieveSocioById: (id) => {
    return api.get(`${endpoints.SOCIO.RETRIEVE_BY_ID}/${id}`);
  },
  
  /**
   * Crea un nuovo socio
   * @param {Object} socioData - Dati del nuovo socio
   * @returns {Promise} Promise con la risposta del server
   */
  createSocio: (socioData) => {
    return api.post(endpoints.SOCIO.CREATE, socioData);
  },
  
  /**
   * Aggiorna i dati di un socio
   * @param {Object} socioData - Dati aggiornati del socio
   * @returns {Promise} Promise con la risposta del server
   */
  updateSocio: (socioData) => {
    return api.post(endpoints.SOCIO.UPDATE, socioData);
  },

  /**
   * Recupera il libro soci per tipo (effettivi, volontari, tesserati)
   * @param {number} tipoSocio - Tipo di socio (1=Effettivi, 2=Volontari, 3=Tesserati)
   * @param {number} annoValidita - Anno di validit
   * @returns {Promise} Promise con i dati del libro soci
   */
  retrieveLibroSoci: (tipoSocio, annoValidita = null) => {
    const currentYear = annoValidita || new Date().getFullYear();
    return api.get(`${endpoints.SOCIO.RETRIEVE_LIBRO_SOCI}/${tipoSocio}/${currentYear}`);
  },

  /**
   * Recupera statistiche sui soci
   * @returns {Promise} Promise con le statistiche
   */
  getStats: () => {
    return api.get(`${endpoints.SOCIO.RETRIEVE_STATS}`);
  }
};

export default socioService;