import React, { useState, useEffect } from 'react';
import { Container, Tabs, Tab, Card, Button, Row, Col, Form, Alert, Table, Modal } from 'react-bootstrap';
import activityService from '../../api/services/activityService';
import parametriService from '../../api/services/parametriService';
import geographicService from '../../api/services/geographicService';
import utilityService from '../../api/services/utilityService';
import TextField from '../../components/forms/TextField';
import SelectField from '../../components/forms/SelectField';
import CheckboxField from '../../components/forms/CheckboxField';
import Loader from '../../components/common/Loader';
import AttivitaList from '../../components/attivita/AttivitaList';
import AttivitaForm from '../../components/attivita/AttivitaForm';
import { formatDateDisplay } from '../../utils/dateUtils';

/**
 * Componente modale per conferma eliminazione
 */
const DeleteConfirmModal = ({ show, onHide, onConfirm, activityName, loading }) => {
  return (
    <Modal show={show} onHide={onHide} centered>
      <Modal.Header closeButton className="bg-danger text-white">
        <Modal.Title>
          <i className="bi bi-exclamation-triangle-fill me-2"></i>
          Conferma Eliminazione
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="text-center">
          <div className="mb-3">
            <i className="bi bi-trash3 text-danger" style={{ fontSize: '3rem' }}></i>
          </div>
          <h5 className="mb-3">Sei sicuro di voler eliminare questa attivit?</h5>
          <div className="alert alert-warning">
            <strong>Attivit: </strong>
            <span className="fw-bold text-danger">{activityName}</span>
          </div>
          <p className="text-muted mb-0">
            <small>
              <i className="bi bi-info-circle me-1"></i>
              Questa azione non pu essere annullata.
            </small>
          </p>
        </div>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={onHide} disabled={loading}>
          <i className="bi bi-x-lg me-1"></i>
          Annulla
        </Button>
        <Button variant="danger" onClick={onConfirm} disabled={loading}>
          {loading ? (
            <>
              <span className="spinner-border spinner-border-sm me-2" role="status"></span>
              Eliminazione...
            </>
          ) : (
            <>
              <i className="bi bi-trash3 me-1"></i>
              Elimina
            </>
          )}
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

/**
 * Pagina per la gestione dei parametri dell'applicazione
 */
const Parametri = () => {
  // Stato per la tab corrente
  const [activeTab, setActiveTab] = useState('attivita');
  
  // Stati per le diverse sezioni
  const [showAttivitaList, setShowAttivitaList] = useState(true);
  const [showAttivitaForm, setShowAttivitaForm] = useState(false);
  const [showComuneSearch, setShowComuneSearch] = useState(false);
  const [showNumeroTessera, setShowNumeroTessera] = useState(false);
  
  // Stati per i dati
  const [parameters, setParameters] = useState([]);
  const [sezioni, setSezioni] = useState([]);
  const [federazioni, setFederazioni] = useState([]);
  const [selectedSezione, setSelectedSezione] = useState(null);
  const [activities, setActivities] = useState([]);
  const [selectedActivity, setSelectedActivity] = useState(null);
  const [updateMode, setUpdateMode] = useState(false);
  
  // Stati per la ricerca comune
  const [nomecomune, setNomecomune] = useState('');
  const [comuni, setComuni] = useState([]);
  
  // Stati per numero tessera
  const [numeroTesseraSearch, setNumeroTesseraSearch] = useState('');
  const [abbonamenti, setAbbonamenti] = useState([]);
  const [selectedAbbonamento, setSelectedAbbonamento] = useState(null);
  const [numeroTesseraUpdate, setNumeroTesseraUpdate] = useState('');
  const [updateOptions, setUpdateOptions] = useState({
    updCtr: false,
    updEmpty: true
  });
  const [showUpdateTessera, setShowUpdateTessera] = useState(false);
  
  // Stati per il modale di eliminazione
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [activityToDelete, setActivityToDelete] = useState(null);
  const [deleteLoading, setDeleteLoading] = useState(false);
  
  // Stati per errori e loading
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [showAlert, setShowAlert] = useState(false);
  const [alertVariant, setAlertVariant] = useState('danger');
  
  // Carica i dati iniziali
  useEffect(() => {
    const fetchInitialData = async () => {
      try {
        setLoading(true);
        
        // Carica sezioni e federazioni in parallelo
        const [sezioniResponse, federazioniResponse] = await Promise.all([
          activityService.retrieveSezioni(),
          activityService.retrieveFamilies()
        ]);
        
        setSezioni(sezioniResponse.data.data || []);
        setFederazioni(federazioniResponse.data.data || []);
        
        setLoading(false);
      } catch (err) {
        console.error('Errore nel caricamento dei dati iniziali:', err);
        setError('Si  verificato un errore nel caricamento dei dati.');
        setAlertVariant('danger');
        setShowAlert(true);
        setLoading(false);
      }
    };
    
    fetchInitialData();
  }, []);
  
  // Gestione del cambio tab
  const handleTabChange = (key) => {
    setActiveTab(key);
    
    // Reset stati
    setShowAttivitaList(true);
    setShowAttivitaForm(false);
    setShowComuneSearch(false);
    setShowNumeroTessera(false);
    setSelectedSezione(null);
    setSelectedActivity(null);
    setActivities([]);
    setNomecomune('');
    setComuni([]);
    setNumeroTesseraSearch('');
    setAbbonamenti([]);
    setSelectedAbbonamento(null);
    setNumeroTesseraUpdate('');
    setShowUpdateTessera(false);
    
    if (key === 'comune') {
      setShowComuneSearch(true);
    } else if (key === 'numeroTessera') {
      setShowNumeroTessera(true);
    }
  };
  
  // Funzione per arricchire le attivit con i nomi delle federazioni
  const enrichActivitiesWithFederationNames = (activities) => {
    return activities.map(activity => {
      const federazione = federazioni.find(f => f.id === activity.federazioneId);
      return {
        ...activity,
        federazioneNome: federazione ? federazione.nome : 'Non assegnata'
      };
    });
  };
  
  // Gestione selezione sezione
  const handleSezioneChange = async (name, selectedValue) => {
    const sezioneSelezionata = sezioni.find(s => s.nome === selectedValue.value.value);
    setSelectedSezione(selectedValue.value);
    setSelectedActivity(null);
    
    try {
      setLoading(true);
      const response = await activityService.retrieveActivitiesBySezione(sezioneSelezionata.id);
      
      // Arricchisci le attivit con i nomi delle federazioni
      const enrichedActivities = enrichActivitiesWithFederationNames(response.data.data || []);
      setActivities(enrichedActivities);
      
      setLoading(false);
    } catch (err) {
      console.error('Errore nel caricamento delle attivit:', err);
      setError('Si  verificato un errore nel caricamento delle attivit.');
      setAlertVariant('danger');
      setShowAlert(true);
      setLoading(false);
    }
  };
  
  // Gestione creazione attivit
  const handleCreateAttivita = () => {
    setSelectedActivity(null);
    setUpdateMode(false);
    setShowAttivitaList(false);
    setShowAttivitaForm(true);
  };
  
  // Gestione chiusura form attivit
  const handleCloseAttivitaForm = async () => {
    setShowAttivitaForm(false);
    setShowAttivitaList(true);
    
    // Ricarica le attivit se c' una sezione selezionata
    if (selectedSezione) {
      try {
        const response = await activityService.retrieveActivitiesBySezione(sezioni.find(s=>s.nome===selectedSezione.value).id);
        const enrichedActivities = enrichActivitiesWithFederationNames(response.data.data);
        setActivities(enrichedActivities);
      } catch (err) {
        console.error('Errore nel ricaricamento delle attivit:', err);
      }
    }
  };
  
  // Gestione selezione attivit
  const handleActivitySelected = (activity) => {
    setSelectedActivity(activity);
    setUpdateMode(true);
    setShowAttivitaList(false);
    setShowAttivitaForm(true);
  };
  
  // Gestione apertura modale eliminazione
  const handleShowDeleteModal = (activity) => {
    setActivityToDelete(activity);
    setShowDeleteModal(true);
  };
  
  // Gestione chiusura modale eliminazione
  const handleCloseDeleteModal = () => {
    setShowDeleteModal(false);
    setActivityToDelete(null);
    setDeleteLoading(false);
  };
  
  // Gestione eliminazione attivit confermata
  const handleConfirmDelete = async () => {
    if (!activityToDelete) return;
    
    try {
      setDeleteLoading(true);
      
      const response = await activityService.removeActivity({ attId: activityToDelete.id });
      
      if (response.data.data) {
        setSuccess('Attivit eliminata con successo.');
        setAlertVariant('success');
        setShowAlert(true);
        
        // Ricarica le attivit
        const activitiesResponse = await activityService.retrieveActivitiesBySezione(sezioni.find(s=>s.nome===selectedSezione.value).id);
        const enrichedActivities = enrichActivitiesWithFederationNames(activitiesResponse.data.data);
        setActivities(enrichedActivities);
        
        // Chiudi il modale
        handleCloseDeleteModal();
      } else {
        throw new Error(response.data.message || 'Errore durante l\'eliminazione');
      }
      
    } catch (err) {
      console.error('Errore nell\'eliminazione dell\'attivit:', err);
      setError(err.message || 'Si  verificato un errore durante l\'eliminazione dell\'attivit.');
      setAlertVariant('danger');
      setShowAlert(true);
      setDeleteLoading(false);
    }
  };
  
  return (
    <Container className="mt-4 mb-5">
      <h2 className="mb-4">Parametri</h2>
      
      {showAlert && (
        <Alert 
          variant={alertVariant} 
          onClose={() => setShowAlert(false)} 
          dismissible
        >
          {alertVariant === 'danger' ? error : success}
        </Alert>
      )}
      
      {loading && <Loader />}
      
      {/* Modale di conferma eliminazione */}
      <DeleteConfirmModal
        show={showDeleteModal}
        onHide={handleCloseDeleteModal}
        onConfirm={handleConfirmDelete}
        activityName={activityToDelete?.nome || ''}
        loading={deleteLoading}
      />
      
      <Tabs
        activeKey={activeTab}
        onSelect={handleTabChange}
        className="mb-4"
      >
        <Tab eventKey="attivita" title="Attivit">
          {showAttivitaList && (
            <Card>
              <Card.Header className="d-flex justify-content-between align-items-center">
                <h5>Gestione Attivit per Sezione</h5>
                <Button 
                  variant="success" 
                  onClick={handleCreateAttivita}
                  disabled={!selectedSezione}
                >
                  Nuova Attivit
                </Button>
              </Card.Header>
              <Card.Body>
                <Form>
                  <SelectField
                    label="Sezione"
                    name="sezione"
                    value={selectedSezione ? selectedSezione.nome : ''}
                    options={sezioni.map(sezione => sezione.nome)}
                    onChange={handleSezioneChange}
                    placeholder="Seleziona una sezione"
                    required
                  />
                </Form>
                
                
                {selectedSezione && activities.length > 0 && (
                  <div className="mt-4">
                    <Table striped bordered hover responsive>
                      <thead className="table-dark">
                        <tr>
                          <th>ID</th>
                          <th>Nome</th>
                          <th>Federazione</th>
                          <th>Codice</th>
                          <th>Email Referente</th>
                          <th>Azioni</th>
                        </tr>
                      </thead>
                      <tbody>
                        {activities.map((activity) => (
                          <tr key={activity.id}>
                            <td>{activity.id}</td>
                            <td><strong>{activity.nome}</strong></td>
                            <td>
                              {activity.federazioneNome ? (
                                <span className="badge bg-primary">{activity.federazioneNome}</span>
                              ) : (
                                <span className="text-muted">Non assegnata</span>
                              )}
                            </td>
                            <td>
                              {activity.codice ? (
                                <span className="badge bg-secondary">{activity.codice}</span>
                              ) : (
                                <span className="text-muted">-</span>
                              )}
                            </td>
                            <td>
                              {activity.emailReferente ? (
                                <a href={`mailto:${activity.emailReferente}`}>
                                  {activity.emailReferente}
                                </a>
                              ) : (
                                <span className="text-muted">-</span>
                              )}
                            </td>
                            <td>
                              <div className="d-flex gap-2">
                                <Button 
                                  variant="primary" 
                                  size="sm"
                                  onClick={() => handleActivitySelected(activity)}
                                >
                                  Modifica
                                </Button>
                                <Button 
                                  variant="outline-danger" 
                                  size="sm"
                                  onClick={() => handleShowDeleteModal(activity)}
                                >
                                  Elimina
                                </Button>
                              </div>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </Table>
                  </div>
                )}
                
                {selectedSezione && activities.length === 0 && (
                  <div className="text-center mt-4">
                    <p className="text-muted">Nessuna attivit trovata per questa sezione.</p>
                    <p className="text-muted">Clicca su "Nuova Attivit" per crearne una.</p>
                  </div>
                )}
              </Card.Body>
            </Card>
          )}
          
          {showAttivitaForm && (
            <AttivitaFormSezione 
              activity={selectedActivity}
              sezione={selectedSezione}
              federazioni={federazioni}
              updateMode={updateMode}
              onClose={handleCloseAttivitaForm}
              onSuccess={(message) => {
                setSuccess(message);
                setAlertVariant('success');
                setShowAlert(true);
              }}
              onError={(message) => {
                setError(message);
                setAlertVariant('danger');
                setShowAlert(true);
              }}
            />
          )}
        </Tab> 
      </Tabs>
    </Container>
  );
};

/**
 * Componente per il form di creazione/modifica attivit per sezione
 */
const AttivitaFormSezione = ({ activity, sezione, federazioni, updateMode = false, onClose, onSuccess, onError }) => {
  const [formData, setFormData] = useState({
    nome: '',
    codice: '',
    emailReferente: ''
  });
  
  const [selectedFederazione, setSelectedFederazione] = useState(null);
  const [selectedSezione, setSelectedSezione] = useState(null);
  const [sezioni, setSezioni] = useState([]);
  const [loading, setLoading] = useState(false);
  
  // Carica sezioni all'avvio
  useEffect(() => {
    const fetchSezioni = async () => {
      try {
        const response = await activityService.retrieveSezioni();
        setSezioni(response.data.data || []);
      } catch (error) {
        console.error('Errore nel caricamento delle sezioni:', error);
        onError('Errore nel caricamento delle sezioni.');
      }
    };
    
    fetchSezioni();
  }, []);
  
  // Imposta i dati iniziali quando il componente viene montato o l'attivit cambia
  useEffect(() => {
    // Imposta sempre la sezione corrente come default
    setSelectedSezione(sezione);
    if (activity && updateMode) {
      setFormData({
        nome: activity.nome || '',
        codice: activity.codice || '',
        emailReferente: activity.emailReferente || ''
      });
      
      // Trova la federazione corrispondente
      if (activity.federazioneId && federazioni.length > 0) {
        const federazione = federazioni.find(f => f.id === activity.federazioneId);
        if (federazione){
          const formattedFederazione = {
            id: federazione.id,
            label: federazione.nome
          };
          setSelectedFederazione(formattedFederazione);
        }
      }
      
      // Trova la sezione corrispondente (se diversa da quella corrente)
      if (activity.sezioneId && sezioni.length > 0) {
        const sezioneAttuale = sezioni.find(s => s.id === activity.sezioneId);
        if (sezioneAttuale) {
          const formattedSezione = {
            id: sezioneAttuale.id,
            label: sezioneAttuale.nome
          };
          setSelectedSezione(formattedSezione);
        }
      }
    } else {
      // Reset per nuova creazione
      setFormData({
        nome: '',
        codice: '',
        emailReferente: ''
      });
      setSelectedFederazione(null);
      setSelectedSezione(sezione);
    }
  }, [activity, updateMode, federazioni, sezioni, sezione]);
  
  const handleChange = (name, value) => {
    setFormData(prev => ({ ...prev, [name]: value }));
  };
  
  const handleFederazioneChange = (name, selectedValue) => {
    setSelectedFederazione(selectedValue.value);
  };
  
  const handleSezioneChange = (name, selectedValue) => {
    setSelectedSezione(selectedValue.value);
    
  };
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!formData.nome.trim()) {
      onError('Il nome dell\'attivit  obbligatorio.');
      return;
    }

    
    if (!selectedFederazione) {
      onError('Selezionare una federazione per l\'attivit.');
      return;
    }
    
    if (!selectedSezione) {
      onError('Selezionare una sezione per l\'attivit.');
      return;
    }
    
    try {
      setLoading(true);
      console.log(selectedFederazione,selectedSezione)
      const body = {
        nome: formData.nome.trim(),
        federazioneId: federazioni.find(f=>f.nome===selectedFederazione.label).id,
        sezioneId: sezioni.find(s=>s.nome===selectedSezione.label).id,
        codice: formData.codice.trim() || null,
        emailReferente: formData.emailReferente.trim() || null
      };
      
      if (updateMode && activity) {
        body.id = activity.id;
      }
      
      console.log('Dati da inviare:', body,updateMode);
      
      const response = await activityService.updateActivity(body);
      
      if (response.data.data) {
        onSuccess(updateMode ? 'Attivit aggiornata con successo.' : 'Attivit creata con successo.');
        
        if (!updateMode) {
          // Reset form per nuova creazione
          setFormData({
            nome: '',
            codice: '',
            emailReferente: ''
          });
          setSelectedFederazione(null);
        }
        
        // Chiudi il form dopo un piccolo delay per permettere all'utente di vedere il messaggio
        setTimeout(() => {
          onClose();
        }, 1500);
      } else {
        throw new Error(response.data.message || 'Errore durante il salvataggio');
      }
      
      setLoading(false);
    } catch (error) {
      console.error('Errore nel salvataggio dell\'attivit:', error);
      onError(error.message || 'Si  verificato un errore durante il salvataggio dell\'attivit.');
      setLoading(false);
    }
  };
  
  return (
    <Card>
      <Card.Header className="d-flex justify-content-between align-items-center">
        <h5>{updateMode ? 'Modifica Attivit' : 'Nuova Attivit'}</h5>
        <Button variant="secondary" onClick={onClose} disabled={loading}>
          Chiudi
        </Button>
      </Card.Header>
      <Card.Body>
        
        <Form onSubmit={handleSubmit}>
          
          <Row className="mb-3">
            <Col md={6}>
              <SelectField
                label="Sezione"
                name="sezione"
                value={selectedSezione}
                options={sezioni.map(sez => sez.nome)}
                onChange={handleSezioneChange}
                placeholder="Seleziona una sezione"
                required
              />
            </Col>
            <Col md={6}>
              <SelectField
                label="Federazione"
                name="federazione"
                value={selectedFederazione}
                options={federazioni.map(fed => fed.nome)}
                onChange={handleFederazioneChange}
                placeholder="Seleziona una federazione"
                required
              />
            </Col>
          </Row>
          
          <Row className="mb-3">
            <Col>
              <TextField
                label="Nome Attivit"
                name="nome"
                value={formData.nome}
                onChange={handleChange}
                required
              />
            </Col>
          </Row>
          
          <Row className="mb-3">
            <Col md={6}>
              <TextField
                label="Codice"
                name="codice"
                value={formData.codice}
                onChange={handleChange}
                placeholder="Codice identificativo (opzionale)"
              />
            </Col>
            <Col md={6}>
              <TextField
                label="Email Referente"
                name="emailReferente"
                type="email"
                value={formData.emailReferente}
                onChange={handleChange}
                placeholder="email@esempio.it (opzionale)"
              />
            </Col>
          </Row>
          
          <div className="d-flex justify-content-end mt-4">
            <Button 
              variant="primary" 
              type="submit"
              disabled={loading}
            >
              {loading ? (
                <>
                  <span className="spinner-border spinner-border-sm me-2" role="status"></span>
                  Salvataggio...
                </>
              ) : (
                updateMode ? 'Aggiorna' : 'Crea'
              )}
            </Button>
          </div>
        </Form>
      </Card.Body>
    </Card>
  );
};

export default Parametri;